import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import AddRecipe from "./addRecipe";
import {
    addNewRecipeDessert,
    addNewRecipeDessertDB,
    addNewRecipeMain,
    addNewRecipeMainDB
} from "../../redux/bookReduser";
import {addRecipeNotices,  timerAddRecipe} from "../../redux/authReduser";






class AddRecipeContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <AddRecipe addNewRecipeDessert={this.props.addNewRecipeDessert}
                       collectionData={this.props.collectionData}
                       addNewRecipeMain={this.props.addNewRecipeMain}
                       timerAddRecipe={this.props.timerAddRecipe}
                       addNewRecipeMainDB={this.props.addNewRecipeMainDB}
                       addNewRecipeDessertDB={this.props.addNewRecipeDessertDB}
                       />
        )
    }



}

let mapStateToProps = (state) => {
    return {

    }
}


export default compose(
    connect(mapStateToProps , {
        addNewRecipeMain,addNewRecipeDessert,timerAddRecipe,addNewRecipeDessertDB,addNewRecipeMainDB
    })
)(AddRecipeContainer)

