import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import Edit from "./edit";

import {withRouter} from "react-router-dom";
import {
    changePosterRecipeMainDB,
    changeRecipeMainDB,
    removePosterRecipeMainDB,
    removeRecipeMainDB
} from "../../redux/bookReduser";






class EditContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <Edit mainData={this.props.mainData} changeRecipeMainDB={this.props.changeRecipeMainDB}
                  params={this.props.match.params} changePosterRecipeMainDB={this.props.changePosterRecipeMainDB}
                  removePosterRecipeMainDB={this.props.removePosterRecipeMainDB} removeRecipeMainDB={this.props.removeRecipeMainDB} />
        )
    }



}

let mapStateToProps = (state) => {
    return {
        mainData: state.Book.mainData
    }
}

let WithRouterEdit = withRouter(EditContainer)

export default compose(
    connect(mapStateToProps , {
        changeRecipeMainDB,changePosterRecipeMainDB,
        removeRecipeMainDB,removePosterRecipeMainDB
    })
)(WithRouterEdit)

