const bookReducer = (state = initialState, action) =>  {
    switch (action.type) {
        case ADD_NEW_RECIPE_MAIN:
            let dataDishes = action.obj
            return {
                ...state,
                mainData: { ...state.mainData, dataDishes}
            }
        case ADD_NEW_RECIPE_DESSERT:
            let dataDessert = action.obj
            return {
                ...state,
                mainData: { ...state.mainData, dataDessert}
            }
        case ADD_NEW_RECIPE:
            let mainData = action.obj
           /* let newMainData = mainData.dataDessert.map(e => {
                let image = ''
                firebase.storage().ref('images/dessert').child(e.id).getDownloadURL().then( url => {
                    image = url
                })
                return {...e, poster:  image}
            })
            let newMainData2 = mainData.dataDishes.map(e => {
                let image = ''
                firebase.storage().ref('images/dishes').child(e.id).getDownloadURL().then( url => {
                    image = url
                })
                return {...e, poster:  image}
            })*/
            return {
                ...state,
                mainData
            }
        case SEND_POSTER_MAIN:
            let poster = action.poster;
            let id1 = action.id;
            let newA = Object.values(state.mainData.dataDishes)
            let newData = newA.map( (e) => {
                if(e.id == id1) {
                    return {...e, poster: poster}
                }else {
                    return e
                }
            })
            return {
                ...state,
                mainData: { ...state.mainData, dataDishes: newData}
            }
        case SEND_POSTER_DESSERT:
            let poster2 = action.poster;
            let id2 = action.id;
            let newD = Object.values(state.mainData.dataDessert)
            let newData2 = newD.map( (e) => {
                if(e.id == id2) {
                    return {...e, poster: poster2}
                }else {
                    return e
                }
            })
            return {
                ...state,
                mainData: { ...state.mainData, dataDessert: newData2}
            }
        default:
            return state;
    };
};


export let addNewRecipe = (obj) =>{
    return {
        type: ADD_NEW_RECIPE,
        obj
    }
}
export let addNewRecipeMain = (obj) =>{
    return {
        type: ADD_NEW_RECIPE_MAIN,
        obj
    }
}
export let addNewRecipeDessert = (obj) =>{
    return {
        type: ADD_NEW_RECIPE_DESSERT,
        obj
    }
}

export let sendPosterM = (id,poster) =>{
    return {
        type: SEND_POSTER_MAIN,
        id,poster
    }
}
export let sendPosterD = (id,poster) =>{
    return {
        type: SEND_POSTER_DESSERT,
        id,poster
    }
}

export let GetDataFirebase = () =>{
    return async (dispatch) => {
        const db = firebase.database();
        let recipe = db.ref('mainData')
        recipe.on('value', (elem) => {
            dispatch(addNewRecipe(elem.val()))
            dispatch(addImageEat(elem.val()))
        })

    }
}

