export let addNewRecipeDessertDB = (obj,poster) => {
    return async (dispatch) => {
        const db = firebase.database();

        await db.ref('mainData/dataDessert/' + obj.id).set(obj)

       const uploadTask =  firebase.storage().ref('images/dessert/' + obj.id ).put(poster)


        uploadTask.on('state_changed' ,
            (snapshot) => {
               let progress = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes)*100)
                // dispatch(progressPoster1(progress))
                console.log(snapshot)
            },
            (error) => {console.log(error)},
            () => {
                console.log('redy')
            })


    }
}

