export let changePosterRecipeMainDB = (id,url,poster) => {
    return async (dispatch) => {
        let local_url = ''
        if(url == 0){
            local_url = 'dishes/'
        }else{
            local_url = 'dessert/'
        }
        const uploadTask =  firebase.storage().ref('images/'+ local_url + id ).put(poster)
        uploadTask.on('state_changed' ,
            (snapshot) => {
                let progress = Math.ceil((snapshot.bytesTransferred / snapshot.totalBytes)*100)
                // dispatch(progressPoster1(progress))
                console.log(snapshot)
            },
            (error) => {console.log(error)},
            () => {
                console.log('redy')
            })
    }
}
export let removeRecipeMainDB = (id,url) => {
    return async (dispatch) => {
        let local_url = ''
        if(url == 0){
            local_url = 'dataDishes/'
        }else{
            local_url = 'dataDessert/'
        }
        const db = firebase.database();
        await db.ref('mainData/'+ local_url + id).remove()

    }
}
export let removePosterRecipeMainDB = (id,url) => {
    return async (dispatch) => {
        let local_url = ''
        if(url == 0){
            local_url = 'dishes/'
        }else{
            local_url = 'dessert/'
        }
        await firebase.storage().ref('images/'+ local_url + id ).delete()



    }
}
