import React from 'react';
import style from '../listOfDishes.module.css'
import {NavLink} from "react-router-dom";



function  ListOfDishesElement(props) {
    let url = 1;
    if(props.param == "/mainDishes"){
        url = 0;
    }

    let listIngredients = ''
    for(let i = 0; i < props.data.ingredients.length; i++){
        listIngredients+= props.data.ingredients[i].name + ' ';

    }


    let newIngredients = props.data.ingredients.map(e => {
        return <span>{e.name} </span>
    })
    return(
        <NavLink className={style.elemMain}   to={props.param + '/'+url +'_pages/' + props.data.id}>
            <div className={style.elemVspl}>
                время приготовления: {props.data.time}мин.
            </div>
            <img className={style.elemImg} src={props.data.poster} alt=""/>
            <div className={style.elemConText}>
                <h4 className={style.elemZag}>{props.data.name}</h4>
                <p data-title={listIngredients} className={style.elemIng}>ингридиенты: {newIngredients}</p>
            </div>
        </NavLink>
    )


}




export default ListOfDishesElement