import React, {useEffect, useState} from 'react';
import style from './listOfDishes.module.css'
import ListOfDishesElement from './listOfDishesElement/listOfDishesElement'
import lypa from '../../files/magnifying-glass (1).svg'
import {NavLink} from "react-router-dom";
import SVG_preloader from "../../files/Bean Eater-1s-200px.svg"

function ListOfDishes(props) {


    useEffect(()=>{
        setObj(props.data)
        if(props.data.length == 0){
            setPreloader(true)
        }else {
            setPreloader(false)

        }
    }, [props])

    let newObject = []
    const [obj,setObj] = useState(props.data)
    const [preloader,setPreloader] = useState(false)
    const [show,setShow] = useState(false)
    const [filterCheckT,setFilterCheckT] = useState(true)
    const [filterCheckH,setFilterCheckH] = useState(true)
    const [filterCheckC,setFilterCheckC] = useState(true)
    let styleA = {
        backgroundColor: "#F5E0A3",
        borderTopLeftRadius: "15px",
        borderTopRightRadius: "15px",
        color: "#200A3E"
    };
    let styleC = {}
    if(show){
        styleC = styleA
    }else{
        styleC = {}
    }

    let needData = props.data


    const changeObj = (e) => {
        let text = e.target.value;
        setObj(needData.filter( (e) => {
            if((e.name.includes(text)) || (e.description.includes(text))){
                return e
            }
        }))
    }


    const clickPoSortirovke = () =>{
        setShow(!show)
    }
    const filterTime = () =>{
       /* if(filterCheckT){
            for(let i=0;i < needData.length-1;i++){
                for(let j=0;j < needData.length - 1 - i;j++){
                    if(needData[j].time > needData[j+1].time){
                        let obj = needData[j]
                        needData[j] = needData[j+1]
                        needData[j+1] = obj
                    }
                }
            }
            setFilterCheckT(!filterCheckT)
            setFilterCheckH(true)
            setFilterCheckC(true)
        }else{
            needData.reverse()
        }*/

        if(filterCheckT){
            for(let i=0;i < needData.length-1;i++){
                for(let j=0;j < needData.length - 1 - i;j++){
                    if(needData[j].time > needData[j+1].time){
                        let obj = needData[j]
                        needData[j] = needData[j+1]
                        needData[j+1] = obj
                    }
                }
            }
            setFilterCheckT(!filterCheckT)
        }else{
            for(let i=0;i < needData.length-1;i++){
                for(let j=0;j < needData.length - 1 - i;j++){
                    if(needData[j].time < needData[j+1].time){
                        let obj = needData[j]
                        needData[j] = needData[j+1]
                        needData[j+1] = obj
                    }
                }
            }
            setFilterCheckT(!filterCheckT)
        }

        setObj(needData.filter((e)=>{
            return e
        }))

    }
    const filterHard = () =>{
        if(filterCheckH){
            for(let i=0;i < needData.length-1;i++){
                for(let j=0;j < needData.length - 1 - i;j++){
                    if(needData[j].hard > needData[j+1].hard){
                        let obj = needData[j+1]
                        needData[j+1] = needData[j]
                        needData[j] = obj
                    }
                }
            }
            setFilterCheckH(!filterCheckH)
        }else{

                for (let i = 0; i < needData.length - 1; i++) {
                    for (let j = 0; j < needData.length - 1 - i; j++) {
                        if (needData[j].hard < needData[j + 1].hard) {
                            let obj = needData[j + 1]
                            needData[j + 1] = needData[j]
                            needData[j] = obj
                        }
                    }
                }
                setFilterCheckH(!filterCheckH)

        }

        setObj(needData.filter((e)=>{
            return e
        }))
    }
    const filterCost = () =>{
        if(filterCheckC){
            for(let i=0;i < needData.length-1;i++){
                for(let j=0;j < needData.length - 1 - i;j++){
                    if(needData[j].cost > needData[j+1].cost){
                        let obj = needData[j]
                        needData[j] = needData[j+1]
                        needData[j+1] = obj
                    }
                }
            }
            setFilterCheckC(!filterCheckC)
        }else{
            for(let i=0;i < needData.length-1;i++){
                for(let j=0;j < needData.length - 1 - i;j++){
                    if(needData[j].cost < needData[j+1].cost){
                        let obj = needData[j]
                        needData[j] = needData[j+1]
                        needData[j+1] = obj
                    }
                }
            }
            setFilterCheckC(!filterCheckC)
        }

        setObj(needData.filter((e)=>{
            return e
        }))
    }
    let meme = []
    if(obj){
        meme = obj.map(e => {
            return <ListOfDishesElement key={e.id}  param={props.param} data={e} />
        })

    }

    return(
        <div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.heading}>
                        <div className={style.search}>
                            <input onChange={changeObj} type="text"/>
                            <img className={style.lypa} src={lypa} alt=""/>
                        </div>
                        <div className={style.okoloFillter}>
                            <div className={style.fillter}>
                                <div style={styleC} onClick={obj && clickPoSortirovke} className={style.button}>
                                    сортировка
                                </div>
                                <NavLink to={'/addRecipe'} className={style.back}>
                                    добавить рецепт
                                </NavLink>
                                <NavLink to={'/'} className={style.back}>
                                    назад
                                </NavLink>
                            </div>
                            {show &&
                                <div className={style.showFillter}>
                                    <div onClick={filterTime} className={style.Fillter_time}>
                                        по времени {filterCheckT ? <b>&#9660;</b>
                                        :
                                        <b>&#9650;</b>}
                                    </div>
                                    <div onClick={filterHard} className={style.Fillter_hard}>
                                        по сложности {filterCheckH ? <b>&#9660;</b>
                                        :
                                        <b>&#9650;</b>}
                                    </div>
                                    <div onClick={filterCost} className={style.Fillter_cost}>
                                        по стоймости{filterCheckC ? <b>&#9660;</b>
                                        :
                                        <b>&#9650;</b>}
                                    </div>
                                </div>
                            }

                        </div>
                    </div>
                    <div className={style.list}>
                        {meme.length > 0  ? meme : <div className={style.no_find}>ничего не найдено</div>}
                    </div>
                    {preloader && <div className={style.preloader_container}><img className={style.preloader_style} src={SVG_preloader} alt=""/></div>}
                </div>
            </div>
        </div>
    )


}




export default ListOfDishes