import React, {useRef, useState} from 'react';
import style from './addRecipe.module.css'
import {NavLink, Redirect} from "react-router-dom";
import {Field, reduxForm} from "redux-form";
import random from "../../files/randomEat.jpg";



function AddForm (props) {

    const ingridient_name = useRef(null);
    const ingridient_quantity = useRef(null);
    const progress_name = useRef(null);
    const progress_move = useRef(null);

    const [ingredient,setIngredient] = useState([])
    const [progress,setProgress] = useState([])




    const addIngridient = () => {
        if((ingridient_name.current.value.length > 0)&&(ingridient_quantity.current.value.length > 0)){
            let obj = {
                name: ingridient_name.current.value,
                quantity: ingridient_quantity.current.value,
                id: Math.floor(Math.random() * 100000000)
            }
            setIngredient([...ingredient, obj ])
            ingridient_name.current.value = ''
            ingridient_quantity.current.value = ''
        }
    }
    const addProgress = () => {
        if((progress_name.current.value.length > 0)&&(progress_move.current.value.length > 0)) {
            let obj = {
                name: progress_name.current.value,
                move: progress_move.current.value,
                id: Math.floor(Math.random() * 100000000)
            }
            setProgress([...progress, obj])
            progress_name.current.value = ''
            progress_move.current.value = ''
        }
    }



    const  deleteIngridient = (e) =>{
        setIngredient(
            ingredient.filter((v)=>{
                if(v.id != e){
                    return v
                }
            })
        )
    }

    const  deleteProgressElem = (e) =>{
        setProgress(
            progress.filter((v)=>{
                if(v.id != e){
                    return v
                }
            })
        )
    }



    let objIngridient = ingredient.map((e)=>{
        return <div key={e.id} className={style.form_ingridient_add_element}>{e.name} ({e.quantity})
            <span className={style.form_ingridient_add_element_delete} onClick={() => deleteIngridient(e.id)}>X</span>
        </div>
    })

    let k = 0
    let objProgress = progress.map((e)=>{
        k++
        return <div key={e.id} className={style.form_progress_add_element}>
            <h3>{k}. {e.name}</h3>
            <p>{e.move}</p>
            <span className={style.form_progress_add_element_delete} onClick={() => deleteProgressElem(e.id)}>X</span>
        </div>
    })


    return (
        <form className={style.formAdd} onSubmit={props.handleSubmit}>
            <span>Название:</span>
            <Field  component={"input"} name={"name"}  className={style.formAdd_input} type="text"/>
            <span>Описание:</span>
            <Field  component={"textarea"}  rows={"7"} name={"description"}  className={style.formAdd_textArea} type="text"/>
            <div className={style.formAdd_con}>
                <span>Цена (от 1 до 10):</span>
                <Field  component={"input"} name={"price"} min={"1"} max={"10"}  className={style.formAdd_input} type="number"/>
            </div>
            <div className={style.formAdd_con}>
                <span>Сложность (от 1 до 10):</span>
                <Field  component={"input"} name={"hard"} min={"1"} max={"10"}  className={style.formAdd_input} type="number"/>
            </div>
            <div className={style.formAdd_con}>
                <span>Время приготовления(мин):</span>
                <Field  component={"input"} name={"timer"} min={"0"} max={"10000"} className={style.formAdd_number} type="number"/>
            </div >
            <div className={style.formAdd_box_con}>
                <span>Постер:</span>
                {/*<Field  component={"input"} onChange={onChange}  className={style.formAdd_photo} type="file"/>*/}
                <input type="file" onChange={props.onChangeFile} name={"poster"}/>
            </div>
            <div className={style.form_ingridients}>
                <span>Добавить нигридиенты</span>
                <div className={style.form_ingridient_obj}>
                    {objIngridient}
                </div>
                <div className={style.form_ingridient_add}>
                    <div className={style.form_ingridient_add_1}>
                        <p>Название:</p>
                        <input ref={ingridient_name}   name={"ingridient_name"}  className={style.formAdd_input} type="text"/>
                    </div>
                    <div className={style.form_ingridient_add_2}>
                        <p>Колличество: </p>
                        <input  ref={ingridient_quantity} name={"ingridient_quantity"} min={"0"} max={"10"} step={"0.1"}  className={style.formAdd_input} type="number"/>
                    </div>
                    <div onClick={addIngridient} className={style.form_ingridient_add_button}>+</div>
                </div>
            </div>

            <div className={style.form_progress}>
                <span>Ход приготовления:</span>
                <div className={style.form_progress_obj}>
                    {objProgress}
                </div>
                <div className={style.form_progress_add}>
                    <div className={style.form_progress_add_1}>
                        <p>Название:</p>
                        <input ref={progress_name}   name={"progress_name"}  className={style.formAdd_input} type="text"/>
                        <div onClick={addProgress} className={style.form_ingridient_add_button}>+</div>
                    </div>
                    <div className={style.form_progress_add_2}>
                        <p>Описание шага: </p>
                        <textarea rows={"4"}  ref={progress_move} name={"progress_move"}  className={style.formAdd_textArea} type="text"/>
                    </div>
                </div>
            </div>

            <button onClick={props.addArrays(ingredient,progress)} className={style.formAdd_button}>Добавить</button>
        </form>
    );
}

const AddReduxForm = reduxForm({
    form: 'addNewRecipe'
})(AddForm)

function AddRecipe(props) {

    /*const NewElem = props.collectionData.map( e => {
        return <CollectionsElements id={e.id} name={e.name} description={e.description} poster={e.poster} />
    })*/
    const [choice,setChoice] = useState(false)
    const [check, setCheck] = useState(true)
    const [error, setError] = useState(false)
    const [imageAsFile, setImageAsFile] = useState('')


    const onChangeFile = (e) =>{
        const image = e.target.files[0];
        setImageAsFile((imageFile ) => image)
    }



    let ingridient = []
    let progress = []
    const  addArrays = (ing,pro) => {
        ingridient = ing
        progress = pro
    }


    const  onSubmitForm = (e) => {
        let name = e.name;
        let description = e.description;
        let price = e.price;
        let hard = e.hard;
        let timer = e.timer;
        let obj = {
            id: Math.floor(Math.random() * 100000000),
            name: name,
            description: description,
            time: timer,
            hard: hard,
            cost: price,
            poster: '',
            ingredients: ingridient,
            progress: progress
        }

        if((name != '')&&(description != '')&&(price != '')&&(hard != '')&&
            (timer != '')&&(ingridient.length > 0)&&(progress.length > 0)){
            if(choice){
                //props.addNewRecipeDessert(obj)
                props.addNewRecipeDessertDB(obj,imageAsFile)
            }else{
                //props.addNewRecipeMain(obj)
                props.addNewRecipeMainDB(obj,imageAsFile)

            }
            props.timerAddRecipe()
            setCheck(false)
        }else{
            setError(true)
        }
    }
    if(!check){
        return <Redirect to="/" />
    }else{
        return(
            <div>
                <div className={style.container}>
                    <div className={style.main}>
                        <NavLink to={"/"} className={style.back}>Назад</NavLink>
                        <div className={style.zagMain}>
                            <h1>Добвить рецепт</h1>
                            {choice ?
                                <span onClick={()=>setChoice(!choice)}>Диссерт</span>
                                :
                                <span onClick={()=>setChoice(!choice)}>Основное блюдо</span>
                            }

                        </div>
                        {error &&
                            <div onClick={()=>setError(false)} className={style.error}>
                                <span className={style.error_span_1}>ошибка, заполните недостоющие окна</span>
                                <span className={style.error_span_2}>закрыть</span>
                            </div>
                        }
                        <AddReduxForm onChangeFile={onChangeFile} addArrays={addArrays}  onSubmit={onSubmitForm} />
                    </div>
                </div>
            </div>
        )
    }



}




export default AddRecipe