import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import ElemPages from "./elemPages";
import {withRouter} from "react-router-dom";
import {GetDataFirebase} from "../../redux/bookReduser";






class ElemPagesContainer extends React.Component{
   componentDidMount() {

   }

    render(){
        return (
            <ElemPages mainData={this.props.mainData} params={this.props.match.params}  />
        )
    }



}

let mapStateToProps = (state) => {
    return {
        mainData: state.Book.mainData
    }
}

let WithRouterElemPages = withRouter(ElemPagesContainer)

export default compose(
    connect(mapStateToProps , {

    })
)(WithRouterElemPages)

