import React, {useState} from 'react';
import style from './elemPages.module.css'
import {NavLink} from "react-router-dom";



function ElemPages(props) {


    const [edinica, setEdinica] = useState(100)
    const [edit, setEdit] = useState(false)
    let dates = []
    let url = ''
    if(props.params.url == "0"){
        dates = Object.values(props.mainData.dataDishes)
        url = '/mainDishes'
    }else {
        url = '/dessert'
        dates = Object.values(props.mainData.dataDessert)
    }

    const NewElem = dates.filter( e => {
        if(e.id == props.params.id){
            return e
        }
    })


    let objIngredients = []
    if(NewElem[0] != null){
        objIngredients = NewElem[0].ingredients.map(e => {
            let num = e.quantity*edinica
            return <div className={style.ingredientsElem}>
                <div>{e.name}</div>
                <div>{num.toFixed(2)}г</div>
            </div>
        })
    }


    let k = 0

    let objProgress = []
    if(NewElem[0] != null) {
        objProgress = NewElem[0].progress.map(e => {
            k++
            return <div className={style.xod_element}>
                <p className={style.one_str}>{k}. {e.name}</p>
                <p className={style.two_str}>{e.move}</p>
            </div>
        })
    }

    const onChangeEdinica = (e) =>{
        if(e.target.value.length < 6){
            setEdinica(e.target.value)
        }

    }


    return(
        <div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.name}>
                        <h1 onClick={() => setEdit(!edit)} title={"Двойной клик - переход к редактированию"}>{NewElem[0] != null && NewElem[0].name}</h1>
                        {edit &&
                            <div className={style.perexod}>
                                <h4>Перейти к редактированию</h4>
                                <NavLink to={props.params.id + "/edit"}>Да</NavLink>
                                <span onClick={() => setEdit(false)}>Нет</span>
                            </div>
                        }
                        <NavLink to={url}  className={style.back}>назад</NavLink>
                    </div>
                    <div className={style.row_1}>
                        <div className={style.row_1_container_img}>
                            <img src={NewElem[0] != null && NewElem[0].poster} alt=""/>
                        </div>
                        <div className={style.row_1_discript}>
                            <p className={style.time}>Время приготовления: <b>{NewElem[0] != null && NewElem[0].time} мин</b> </p>
                            <p>{NewElem[0] != null && NewElem[0].description}</p>
                        </div>
                    </div>
                    <div className={style.row_2_ing}>
                        <div className={style.row_2_zag}>
                            <h2>Нужные ингридиенты</h2>
                            <div className={style.yslovna9Ed}>условная единица: <input onChange={(e) => onChangeEdinica(e)} value={edinica} type="text"/> г</div>
                        </div>
                        <div className={style.ingridients_container}>
                            {objIngredients}
                        </div>
                    </div>
                    <div className={style.row_3_xod}>
                        <h1>ход выполнения</h1>
                        <div className={style.xod_container}>
                            {objProgress}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )


}




export default ElemPages