import React, {useEffect, useRef, useState} from 'react';
import style from './edit.module.css'
import {NavLink, Redirect} from "react-router-dom";
import {Field, reduxForm} from "redux-form";
import random from "../../files/randomEat.jpg";
import {removeRecipeMainDB} from "../../redux/bookReduser";





function Edit(props) {

    let dates = []

    const [nameHook,setNameHook] = useState([])
    const [showName,setShowName] = useState(false)
    const [textName,setTextName] = useState('')

    const [showDescriptions,setShowDescriptions] = useState(false)
    const [textDescriptions,setTextDescriptions] = useState('')

    const [showTime,setShowTime] = useState(false)
    const [textTime,setTextTime] = useState(0)

    const [showHard,setShowHard] = useState(false)
    const [textHard,setTextHard] = useState(0)

    const [showCost,setShowCost] = useState(false)
    const [textCost,setTextCost] = useState(0)

    const [arrIngredients,setArrIngredients] = useState([])
    const [arrProgress,setArrProgress] = useState([])

    const [changePoster,setChangePoster] = useState()

    const [redirectToBack,setRedirectToBack] = useState(false)

    const onChangeFile = (e) =>{
        const image = e.target.files[0];
        setChangePoster((imageFile ) => image)
    }


    useEffect(()=>{
        if(props.params.url == "0"){
            setNameHook( Object.values(props.mainData.dataDishes).filter( e => {
                if(e.id == props.params.id){
                    setArrIngredients(e.ingredients)
                    setArrProgress(e.progress)
                    return e
                }
            }))
        }else {
            setNameHook(Object.values(props.mainData.dataDessert).filter( e => {
                if(e.id == props.params.id){
                    setArrIngredients(e.ingredients)
                    setArrProgress(e.progress)
                    return e
                }
            }))
        }
    }, [props])

    let url = ''
    if(props.params.url == "0"){
        dates = Object.values(props.mainData.dataDishes)
        url = '/mainDishes'
    }else {
        url = '/dessert'
        dates = Object.values(props.mainData.dataDessert)
    }


    const NewElem = dates.filter( e => {
        if(e.id == props.params.id){
            return e
        }
    })


    if(nameHook[0] != NewElem[0]){
        NewElem[0] = nameHook[0]
    }
    const deleteIngridient = (id) => {
        setArrIngredients(e => {
            let ingred = e.filter((m)=>{
                if(m.id != id){
                    return m
                }
            })
            e = ingred
            return e
        })
    }

    const deleteProgressElem = (id) =>{
        setArrProgress( e => {
            let progress = e.filter((m)=>{
                if(m.id != id){
                    return m
                }
            })
            e = progress
            return e
        })
    }

    let ingredientsElem = []
    let progressElem = []  //
    if(NewElem[0] != null){
        /*ingredientsElem = NewElem[0].ingredients.map((e)=>{
            return <div key={e.id} className={style.ingridient_add}>{e.name} ({e.quantity})
                <span className={style.ingridient_add_delete} onClick={() => deleteIngridient(e.id)}>X</span>
            </div>
        })*/
        /*let k = 0
        progressElem = NewElem[0].progress.map((e)=>{
            k++
            return <div key={e.id} className={style.progress_add}>
                <h3>{k}. {e.name}</h3>
                <p>{e.move}</p>
                <span className={style.progress_add_delete} onClick={() => deleteProgressElem(e.id)}>X</span>
            </div>
        })*/
    }
    if(arrIngredients != null){
        ingredientsElem = arrIngredients.map((e)=>{
            return <div key={e.id} className={style.ingridient_add}>{e.name} ({e.quantity})
                <span className={style.ingridient_add_delete} onClick={() => deleteIngridient(e.id)}>X</span>
            </div>
        })
    }
    if(arrProgress != null){
        let k = 0
        progressElem = arrProgress.map((e)=>{
            k++
            return <div key={e.id} className={style.progress_add}>
                <h3>{k}. {e.name}</h3>
                <p>{e.move}</p>
                <span className={style.progress_add_delete} onClick={() => deleteProgressElem(e.id)}>X</span>
            </div>
        })
    }

// name
    const changeName = () =>{
        setShowName(!showName)
        setTextName(nameHook[0].name)

    }
    const blurName = () =>{
        setShowName(!showName)
        setNameHook((e)=>{
            e[0].name = textName
            return e
        })
    }
    const changeNameWithHook = (e) =>{
        let text = e.target.value
        setTextName(text)

    }
//descriptions
    const changeDescriptions = () =>{
        setShowDescriptions(!showDescriptions)
        setTextDescriptions(nameHook[0].description)

    }
    const blurDescriptions = () =>{
        setShowDescriptions(!showDescriptions)
        setNameHook((e)=>{
            e[0].description = textDescriptions
            return e
        })
    }
    const changeDescriptionsWithHook = (e) =>{
        let text = e.target.value
        setTextDescriptions(text)
    }
//time
    const changeTime = () =>{
        setShowTime(!showTime)
        setTextTime(nameHook[0].time)

    }
    const blurTime = () =>{
        setShowTime(!showTime)
        setNameHook((e)=>{
            e[0].time = textTime
            return e
        })
    }
    const changeTimeWithHook = (e) =>{
        let text = e.target.value
        setTextTime(text)
    }
//hard
    const changeHard = () =>{
        setShowHard(!showHard)
        setTextHard(nameHook[0].hard)

    }
    const blurHard = () =>{
        setShowHard(!showHard)
        setNameHook((e)=>{
            e[0].hard = textHard
            return e
        })
    }
    const changeHardWithHook = (e) =>{
        let text = e.target.value
        setTextHard(text)
    }
//cost
    const changeCost = () =>{
        setShowCost(!showCost)
        setTextCost(nameHook[0].cost)

    }
    const blurCost = () =>{
        setShowCost(!showCost)
        setNameHook((e)=>{
            e[0].cost = textCost
            return e
        })
    }
    const changeCostWithHook = (e) =>{
        let text = e.target.value
        setTextCost(text)
    }
// style add ingredients
    const [showsAddIngredients,setShowsAddIngredients] = useState(false)
    const [AddIngredientsName,setAddIngredientsName] = useState('')
    const [AddIngredientsQuantity,setAddIngredientsQuantity] = useState(0)
    let style_ingredients_add_plus = {
        color: "#f8c66b",
        fontSize: "24px",
        display: "flex",
        alignItems: "center",
        border: "1px solid #f8c66b",
        marginRight: "8px",
        borderRadius: "10px",
        padding: "3px 0",
        cursor: "pointer",
        transition: "all 0.3s ease",
        marginBottom: "5px"
    }
    let style_ingredients_add_form = {
        display: "flex",
        border: "1px solid #f8c66b",
        marginRight: "8px",
        marginBottom: "5px",
        borderRadius: "10px",
        transition: "all 0.3s ease",
        alignItems: "center"
    }

    const addIngredientsShow = () =>{
        setShowsAddIngredients(true)
        /*setArrIngredients(nameHook[0].ingredients)*/
    }
    const addIngredients = () => {

        if((AddIngredientsName.length > 0)&&(AddIngredientsQuantity > 0)){
            let obj = {
                name: AddIngredientsName,
                quantity: AddIngredientsQuantity,
                id: Math.floor(Math.random() * 100000000)
            }
            setArrIngredients([...arrIngredients, obj])
            setAddIngredientsName('')
            setAddIngredientsQuantity(0)
            setShowsAddIngredients(false)
        }
    }
    const ChangeShowIngredients = () =>{
        setShowsAddIngredients(false)
    }
    const AddIngredientsNameChange = (e) =>{
        let text = e.target.value
        setAddIngredientsName(text)
    }
    const AddIngredientsQuantityChange = (e) =>{
        let number = e.target.value
        setAddIngredientsQuantity(number)
    }
// style and progress
    const [showsAddProgress,setShowsAddProgress] = useState(false)
    const [AddProgressName,setAddProgressName] = useState('')
    const [AddProgressDescription,setAddProgressDescription] = useState('')
    const onChangeAddProgressName = (e) =>{
        let text = e.target.value
        setAddProgressName(text)
    }
    const onChangeAddProgressDescription = (e) =>{
        let text = e.target.value
        setAddProgressDescription(text)
    }
    const addProgress = () =>{

        if((AddProgressName.length > 0)&&(AddProgressDescription.length > 0)){
            let obj = {
                name: AddProgressName,
                move: AddProgressDescription,
                id: Math.floor(Math.random() * 100000000)
            }
            setArrProgress([...arrProgress, obj])
            setAddProgressName('')
            setAddProgressDescription('')
            ChangeShowProgress()
        }

    }
    const ChangeShowProgress = () =>{
        setShowsAddProgress(!showsAddProgress)
    }
    /*const ChangeShowProgressTrue = () =>{
        setShowsAddProgress(true)
    }*/

//
    const scrollToMyRef = () => {window.scrollTo(0,0)}

    const mainChange = () =>{
        if(NewElem[0] != null) {
            let finalObject = {
                id: NewElem[0].id,
                name: NewElem[0].name,
                description: NewElem[0].description,
                time: NewElem[0].time,
                hard: NewElem[0].hard,
                cost: NewElem[0].cost,
                ingredients: arrIngredients,
                progress: arrProgress
            }
            scrollToMyRef()
            props.changeRecipeMainDB(finalObject,props.params.url)
            if(changePoster != undefined){
                props.changePosterRecipeMainDB(props.params.id,props.params.url,changePoster)
            }
        }
    }

    const deleteRecipe = () =>{
        props.removeRecipeMainDB(props.params.id,props.params.url)
        props.removePosterRecipeMainDB(props.params.id,props.params.url)
        setRedirectToBack(true)
    }
    
    if(redirectToBack){
        return <Redirect to={url}/>
    }else{
        return(
            <div className={style.container}>
                <div className={style.main}>
                    <NavLink to={url +"/"+ props.params.url + "_pages/" + props.params.id} className={style.back}>Назад</NavLink>
                    <span onClick={deleteRecipe} className={style.delete }>Удалить</span>
                    <div className={style.information}>Чтобы изменить элемент - нужно кликнуть на него  </div>
                    <div className={style.poster_container}>
                        <span>Постер:</span>
                        <div className={style.poster_hidden_input}>
                            <input onChange={onChangeFile} className={style.poster_file_input} type="file"/>
                            <span className={style.poster_file_button}>Загрузить каринку</span>
                            <img className={style.poster} src={NewElem[0] != null && NewElem[0].poster} alt="Картинки нет"/>
                        </div>
                    </div>
                    <div className={style.name_container}>
                        <span>Название:</span>
                        {showName ?
                            <input className={style.input_text} autoFocus={true} onChange={changeNameWithHook}
                                   onBlur={blurName} value={textName} type="text"/>
                            :
                            <div onClick={changeName} className={style.name}>{NewElem[0] != null && NewElem[0].name}</div>
                        }

                    </div>
                    <div className={style.description_container}>
                        <span>Описание:</span>
                        {showDescriptions ?
                            <textarea className={style.textarea_text} autoFocus={true}
                                      onChange={changeDescriptionsWithHook} rows={4}
                                      onBlur={blurDescriptions} value={textDescriptions} />
                            :
                            <div onClick={changeDescriptions} className={style.description}>{NewElem[0] != null && NewElem[0].description}</div>
                        }
                    </div>
                    <div className={style.time_container}>
                        <span>Время приготовления:</span>
                        {showTime ?
                            <input className={style.input_text} autoFocus={true} onChange={changeTimeWithHook}
                                   onBlur={blurTime} value={textTime} type="number"/>
                            :
                            <div onClick={changeTime} className={style.time}>{NewElem[0] != null && NewElem[0].time}</div>
                        }
                    </div>
                    <div className={style.hard_container}>
                        <span>Сложность:</span>
                        {showHard ?
                            <input className={style.input_text} autoFocus={true} onChange={changeHardWithHook}
                                   onBlur={blurHard} value={textHard} type="number"/>
                            :
                            <div onClick={changeHard} className={style.hard}>{NewElem[0] != null && NewElem[0].hard}</div>
                        }
                    </div>
                    <div className={style.cost_container}>
                        <span>Цена:</span>
                        {showCost ?
                            <input className={style.input_text} autoFocus={true} onChange={changeCostWithHook}
                                   onBlur={blurCost} value={textCost} type="number"/>
                            :
                            <div onClick={changeCost} className={style.cost}>{NewElem[0] != null && NewElem[0].cost}</div>
                        }
                    </div>
                    <div className={style.ingredients_container}>
                        <span>Ингридиенты:</span>
                        <div className={style.ingredients}>{NewElem[0] != null && ingredientsElem}
                            <div style={!showsAddIngredients ? style_ingredients_add_plus : style_ingredients_add_form}
                                 className={style.ingredients_add} >{!showsAddIngredients ?
                                <span className={style.ingredients_input_add_main} onClick={addIngredientsShow}>+</span>
                                :
                                <div className={style.ingredients_input_container}>
                                    <input placeholder={"Название ингридиента"} value={AddIngredientsName} onChange={AddIngredientsNameChange} className={style.ingredients_input_text} type="text"  />
                                    <input  value={AddIngredientsQuantity} onChange={AddIngredientsQuantityChange} min={"0"} max={"10"} step={"0.1"}  className={style.ingredients_input_number} type="number"  />
                                    <span onClick={addIngredients} className={style.ingredients_input_add}>+</span>
                                    <span onClick={ChangeShowIngredients} className={style.ingredients_input_minus}>-</span>
                                </div>
                            }
                            </div>
                        </div>

                    </div>
                    <div className={style.progress_container}>
                        <span>Прогресс:</span>
                        <div className={style.progress}>{NewElem[0] != null && progressElem}</div>
                        <div  className={showsAddProgress ? style.progress_add_main : style.progress_add_plus}>
                            {showsAddProgress ?
                                <div className={style.progress_container_input}>
                                    <div className={style.progress_container_input_name}>
                                        <input placeholder={"Название Шага"} value={AddProgressName} onChange={onChangeAddProgressName} className={style.progress_input_text} type="text"  />
                                        <div className={style.progress_container_input_buttoncroup}>
                                            <span onClick={addProgress} className={style.progress_input_add}>Добавить</span>
                                            <span onClick={ChangeShowProgress} className={style.progress_input_minus}>-</span>
                                        </div>
                                    </div>
                                    <textarea rows={"2"} placeholder={"Описание"} value={AddProgressDescription} onChange={onChangeAddProgressDescription}  className={style.progress_input_textarea} type="text"  />

                                </div>
                                :
                                <span className={style.progress_add_plusss} onClick={ChangeShowProgress}>+</span>
                            }

                        </div>
                    </div>
                    <div onClick={mainChange} className={style.save}>Сохранить изменения</div>
                </div>
            </div>
        )
    }

}




export default Edit