import React from 'react';
import style from './mainPage.module.css'
import {NavLink} from "react-router-dom";



function MainPage(props) {

    /*const NewElem = props.collectionData.map( e => {
        return <CollectionsElements id={e.id} name={e.name} description={e.description} poster={e.poster} />
    })*/

    return(
        <div>
            <div className={style.container}>
                <div className={style.main}>
                    <div className={style.zagolovok}>
                        <h1>Моя кулинарная книга</h1>
                    </div>
                    <div className={style.sdvoen}>
                        <NavLink to={"/dessert"} className={style.disert}>
                            <span className={style.disert_text}>Сладкое</span>
                        </NavLink>
                        <NavLink to={"/mainDishes"} className={style.osniva}>
                            <span className={style.osnova_text}>Основные блюда</span>
                        </NavLink>
                    </div>
                </div>
            </div>
        </div>
    )


}




export default MainPage