import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import ListOfDishes from "./listOfDishes";
import {withRouter} from "react-router-dom";
import firebase from "firebase";
import {addImageEat} from "../../redux/bookReduser";







class ListOfDishesContainer extends React.Component{


    componentDidMount() {

    }


    render(){
        if((this.props.match.path == '/dessert')){
            if(this.props.mainData != null){
                return (
                    <ListOfDishes   data={this.props.mainData.dataDessert != null && Object.values(this.props.mainData.dataDessert)}
                                    param={this.props.match.path}   />
                )
            }else {
                return (
                    <ListOfDishes   data={this.props.mainData != null && Object.values(this.props.mainData.dataDessert)}
                                    param={this.props.match.path}   />
                )
            }
        }else{
            if(this.props.mainData != null){
                return (
                    <ListOfDishes
                        data={this.props.mainData.dataDishes != null && Object.values(this.props.mainData.dataDishes)}
                        param={this.props.match.path}/>
                )
            }else{
                return (
                    <ListOfDishes
                        data={this.props.mainData != null && Object.values(this.props.mainData.dataDishes)}
                        param={this.props.match.path}/>
                )
            }
        }

    }

}

let mapStateToProps = (state) => {
    return {
        mainData: state.Book.mainData
    }
}
let WithRouterListOfDishes = withRouter(ListOfDishesContainer)

export default compose(
    connect(mapStateToProps , {
        addImageEat
    })
)(WithRouterListOfDishes)

