import React, {useEffect, useState} from 'react';

import './App.css';
import {BrowserRouter, Route, withRouter, HashRouter } from "react-router-dom";
import MainPageContainer from "./component/mainPage/MainPageContainer";
import ListOfDishesContainer from "./component/listOfDishes/listOfDishesContainer";
import WithRouterElemPages from "./component/elemPages/elemPagesContainer";
import AddRecipeContainer from "./component/addRecipe/addRecipeContainer";
import {compose} from "redux";
import {connect} from "react-redux";
import {addRecipeNotices} from "./redux/authReduser";
import { GetDataFirebase} from "./redux/bookReduser";
import WithRouterEdit from "./component/edit/editContainer";


class App extends React.Component {


    componentDidMount(){
        this.props.GetDataFirebase()

    }


    render() {

        return (
            <HashRouter  basename={process.env.PUBLIC_URL}>
                <div className="App">
                    {this.props.check &&
                    <div className="Notices">
                        <div className="Notices_main">
                            <div className="Notices_text">Рецепт добавлен!</div>
                        </div>
                    </div>
                    }
                    <Route exact path="/" render={() => <MainPageContainer/>}/>
                    <Route exact path="/dessert" render={() => <ListOfDishesContainer/>}/>
                    <Route exact path="/mainDishes" render={() => <ListOfDishesContainer/>}/>
                    <Route exact path="/mainDishes/:url?_pages/:id?" render={() => <WithRouterElemPages/>}/>
                    <Route exact path="/dessert/:url?_pages/:id?" render={() => <WithRouterElemPages/>}/>
                    <Route exact path="/addRecipe" render={() => <AddRecipeContainer/>}/>
                    <Route exact path="/dessert/:url?_pages/:id?/edit" render={() => <WithRouterEdit/>}/>
                    <Route exact path="/mainDishes/:url?_pages/:id?/edit" render={() => <WithRouterEdit/>}/>
                </div>
            </HashRouter >
        );
    }
}

let mapStateToProps = (state) => {
    return {
        check: state.isAuthM.add,
        data: state.Book.mainData
    }
}

export default compose(
    connect(mapStateToProps , {
        addRecipeNotices,GetDataFirebase
    })
)(App)


