

const IS_LOGIN_IN = 'IS_LOGIN_IN';
const IS_LOGIN_OUT = 'IS_LOGIN_OUT';
const ADD_RECIPE_NOTICES = 'ADD_RECIPE_NOTICES';





let initialState = {
    authData: {
        /*name: 213, urlAvatar: 123, id: 2*/
    },
    isAuth: false,
    add: false
    /*followingInProgress: [1,2]*/
}



const authReducer = (state = initialState, action) =>  {


    switch (action.type) {
        case  IS_LOGIN_IN:
            return {
                ...state,
                isAuth: true
            }
        case IS_LOGIN_OUT:
            return {
                ...state,
                isAuth: false
            }
        case ADD_RECIPE_NOTICES:
            return {
                ...state,
                add: !state.add
            }
        default:
            return state;
    };
};



export let isLoginIn = () =>{
    return {
        type: IS_LOGIN_IN
    }
}
export let isLoginOut = () =>{
    return {
        type: IS_LOGIN_OUT
    }
}


export let addRecipeNotices = () =>{
    return {
        type: ADD_RECIPE_NOTICES
    }
}
export let timerAddRecipe = () =>{
    return async (dispatch) => {
        dispatch(addRecipeNotices())
        setTimeout(()=>{
            dispatch(addRecipeNotices())
        },7000)
    }
}

export  default authReducer;